import React from "react";

export default function DisplayComponent(props) {
  const { dataLength, sort, search } = props;
  const { value, inputName } = sort;
  const { filter } = search; 
  return (
    <div>
      <h3>Искомая строка: {filter ? filter : "искомая строка не задана"}</h3>
      <hr />
      <h3>
        Поиск по полю: {search.inputName ? search.inputName : "поле не выбрано"}
      </h3>
      <h3>
        Сортировка по полю: {inputName ? inputName : "поле не выбрано"}
      </h3>
      <h3>
        Напраление сортировки: {value === "" ? "направление не задано" : value}
      </h3>
      <hr />
      <h3>Кол-во совпадений: {dataLength ? dataLength : "0"}</h3>
    </div>
  );
}
