export const CHANGE_SEARCH = "CHANGE_TEXT";
export const CHANGE_SORT = "CHANGE_SORT";
export const CHANGE_ACTIVE_INPUT = "CHANGE_ACTIVE_INPUT";
export const CHANGE_CLEAR = "CHANGE_CLEAR";

export const changeSearch = data => ({
  type: CHANGE_SEARCH,
  data: data
});

export const changeSort = flag => ({
  type: CHANGE_SORT,
  data: flag
});

export const changeActiveInput = id => ({
  type: CHANGE_ACTIVE_INPUT,
  data: id
});

export const changeClear = flag => ({
  type: CHANGE_CLEAR,
  data: flag
});