import React from "react";
import HeadComponent from "../HeadComponent";
import { filterData } from "../../utils/filterData";
import BodyComponent from "../BodyComponent";
import { DisplayContainer } from "../../containers/DisplayContainer";
import { InputBtnClearContainer } from "../../containers/InputBtnClearContainer";
import "./style.css";

export default function TableWidgetComponent(props) {
  const { state } = props;
  const key = state.content.search ? state.content.search.inputName : "";
  const value = state.content.search ? state.content.search.filter : "";
  const data = filterData(state.content.content, key, value);

  const nameInput = state.content.nameInput;
  const sort = state.content.sort.value ? state.content.sort.value : state.content.sort.value;

  return (
    <div className="tableWidget">
      <div>
        <div>
          <DisplayContainer dataLength={data.length} />
          <InputBtnClearContainer />
        </div>
        <table>
          <HeadComponent data={state.content.content[0]} />
          <BodyComponent data={data} sort={sort} nameInput={nameInput} value={value} />
        </table>
      </div>
    </div>
  );
}
