import { connect } from "react-redux";
import SearchMenuComponent from "../../components/SearchMenuComponent";
import { changeSearch, changeActiveInput } from "../../store/actions";

const mapStateToProps = state => ({
  activeInput: state.content.activeInput
});

const mapDispatchToProps = dispatch => ({
  handleChangeSearch: (search, name) => dispatch(changeSearch(search, name)),
  handleChangeActiveInput: id => dispatch(changeActiveInput(id))
});

export const SearchMenuContainer = connect(
  mapStateToProps,
  mapDispatchToProps
)(SearchMenuComponent);
