export default class SortCustom {
  constructor(data, nameInput, sort){

    this.state = [...data]
    this.nameInput = nameInput
    this.sort = sort;

    if(this.sort === 'up') {
      return this.switchMethod()
    } else if (this.sort === 'down') {
      return this.switchMethod().reverse()
    } else {
      return this.state
    }
  }
  
  switchMethod = () => {
    if(this.nameInput === 'id' || this.nameInput === 'count') {
      return this.sortNumber(this.state, this.nameInput)
    } else if (this.nameInput === 'name') {
      return this.sortString(this.state, this.nameInput)
    } else if (this.nameInput === 'data') {
      return this.sortDate(this.state, this.nameInput)
    } else {
      return this.state;
    }
  }

  sortNumber = (data, nameInput) => {
    return [...data].sort(function(a, b) {
      return a[nameInput] - b[nameInput];
    });
  };

  sortDate = (data, nameInput) => {
    return [...data].sort(function(a, b) {
      const dateA = new Date(a[nameInput]);
      const dateB = new Date(b[nameInput]);
      return dateA - dateB; //сортировка по возрастающей дате
    });
  };

  sortString = (data, nameInput) => {
    return [...data].sort(function(a, b) {
      const nameA = a[nameInput].toLowerCase();
      const nameB = b[nameInput].toLowerCase();
      if (nameA < nameB)
        //сортируем строки по возрастанию
        return -1;
      if (nameA > nameB) return 1;
      return 0; // Никакой сортировки
    });
  };
}