import { connect } from "react-redux";
import TableWidgetComponent from "../../components/TableWidgetComponent";
import { changeSearch } from "../../store/actions";

const mapStateToProps = state => ({
  state: state
});

const mapDispatchToProps = dispatch => ({
  handleChange: data => dispatch(changeSearch(data))
});

export const TableWidgetContainer = connect(
  mapStateToProps,
  mapDispatchToProps
)(TableWidgetComponent);
