export const initialState = {
  content: [...contentTable],
  sort: {
    value: 'default',
    inputName: ''
  },
  nameInput: "",
  search: {
    filter: "",
    inputName: ""
  },
  activeInput: null
};

export function tableReducers(state = initialState, action) {
  switch (action.type) {
    case CHANGE_SEARCH:
      return { ...state, ...{ search: action.data } };

    case CHANGE_SORT:
      const { flag, placeholder } = action.data;
      return { ...state, ...{ sort: {value: flag, inputName: placeholder} } };

    case CHANGE_ACTIVE_INPUT:
      return { ...state, ...{ activeInput: action.data } };

    case CHANGE_CLEAR:
      return {
        ...state,
        ...{
          sort: {value: action.data, inputName: action.data},
          activeInput: action.data,
          search: { filter: action.data, inputName: action.data }
        }
      };

    default:
      return state;
  }
}
